import React, { lazy, Suspense } from 'react';

const Table = lazy(() => import('./Table'));

const columns = [
  { value: 'Country', align: 'left', width: '25%' },
  { value: 'Cases', align: 'right', width: '25%' },
  { value: 'Deaths', align: 'right', width: '25%' },
  { value: 'Tests', align: 'right', width: '25%' },
];

const NationDailyRecords = ({ nationDailyRecords }) => {
  return (
    <Suspense fallback={<div />}>
      <Table
        columns={columns}
        title="Daily Covid19 Summary"
        rows={nationDailyRecords}
      ></Table>
    </Suspense>
  );
};

export default NationDailyRecords;
